.chat {
  @keyframes closing {
    from {
      display: block;
      visibility: visible;
      opacity: 1;
    }
    to {
      display: none;
      visibility: hidden;
      opacity: 0;
    }
  }

  &__message {
    @include flex-h-end;

    @include high-tablet {
      justify-content: center;
    }
  }

  &__image {
    margin-right: 20px;
    width: 50px;
    height: 50px;

    @include high-tablet {
      margin-right: 15px;
      width: 40px;
      height: 40px;
    }

    @include mobile {
      margin-right: 8px;
      width: 28px;
      height: 28px;
    }
  }

  &__block {
    width: 358px;
    padding-left: 28px;
    padding-right: 28px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    @include high-tablet {
      padding-left: 18px;
      padding-right: 18px;
      width: 300px;
      background-position: top;
    }

    @include mobile {
      padding-right: 10px;
      width: 273px;
    }
  }

  &__user {
    &-block {
      padding-top: 20px;
      min-height: 115px;
      background-image: url("../img/chat-fon-1.png");

      @include high-tablet {
        padding-top: 12px;
      }

      @include mobile {
        min-height: 86px;
      }
    }

    &-text {
      max-width: 270px;
      color: var(--light-color);

      @include mobile {
        max-width: 219px;
        font-size: 14px;
      }
    }
  }

  &__admin {
    &-block {
      padding-top: 50px;
      min-height: 173px;
      background-image: url("../img/chat-fon-2.png");

      @include high-tablet {
        padding-top: 30px;
      }
    }

    &-text {
      width: 100%;

      @include mobile {
        font-size: 14px;
      }
    }

    &-image {
      margin-top: 30px;

      @include mobile {
        margin-top: 21px;
      }
    }
  }
}

.animate__fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;

  @include high-tablet {
    -webkit-animation-name: fadeInDownTablet;
    animation-name: fadeInDownTablet;
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

@keyframes fadeInDownTablet {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}
