.benefits {
  margin-bottom: 36px;

  &__list {
    gap: 20px;
  }

  &__item {
    padding-top: 50px;
    padding-left: 50px;
    border-radius: 20px;

    &-conveniently {
      position: relative;
      padding: 70px 50px;
      grid-column: 12 span;
      background-color: var(--light-color);

      @include high-tablet {
        min-height: 525px;
      }
    }

    &-center {
      grid-column: 4 span;
    }

    &-available {
      background-color: var(--surf-crest-color);
    }

    &-effectively {
      background-color: var(--east-bay-color);
    }

    &-advantageous {
      background-color: var(--havelock-blue-color);
    }

    &-safely {
      position: relative;
      padding-left: 0;
      padding-top: 194px;
      padding-bottom: 174px;
      grid-column: 7/5 span;

      @include high-tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 525px;
      }
    }

    @include high-desktop {
      padding-top: 30px;
      padding-left: 20px;
    }

    @include high-tablet {
      grid-column: 2/6 span;
    }

    @include mobile {
      grid-column: 6 span;
      padding: 30px 20px;
    }
  }

  &__frame {
    display: inline-block;
    margin-bottom: 22px;

    &-advantageous {
      color: var(--text-color);
      background-color: var(--seashell-color);
    }
  }

  &__title {
    margin-bottom: 36px;
    font-size: 36px;

    &-conveniently {
      max-width: 522px;

      @include tablet {
        max-width: 400px;
      }

      @include high-tablet {
        max-width: 450px;
        font-size: 30px;
      }
    }

    &-center {
      font-size: 30px;
      color: var(--seashell-color);
    }

    &-available {
      max-width: 313px;
      color: var(--text-color);
    }

    &-safely {
      margin-bottom: 36px;
      max-width: 420px;
      font-size: 40px;

      @include high-tablet {
        text-align: center;
        max-width: 350px;
        font-size: 30px;
      }

      @include high-tablet {
        margin-bottom: 20px;
      }

      @include mobile {
        font-size: 24px;
      }
    }

    @include high-tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__text {
    max-width: 421px;
    font-size: 20px;

    &-center {
      font-size: 18px;
      color: var(--seashell-color);
    }

    &-available {
      max-width: 313px;
      color: var(--text-color);
    }

    &-advantageous {
      max-width: 300px;
    }

    &-safely {
      max-width: 450px;
      font-size: 24px;

      @include high-tablet {
        text-align: center;
      }

      @include mobile {
        font-size: 18px;
      }
    }

    @include high-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  &__image {
    display: block;
    margin: 0 auto;

    &-conveniently {
      position: absolute;
      top: -45px;
      right: 81px;

      @include high-tablet {
        top: 50%;
        right: 50%;
        width: 240px;
        height: 240px;
        transform: translateX(50%);
      }
    }

    &-safely {
      position: absolute;
      top: 110px;
      right: 450px;

      @include high-desktop {
        top: 0;
      }

      @include high-tablet {
        top: 50%;
        right: 50%;
        width: 240px;
        height: 240px;
        transform: translateX(50%);
      }
    }
  }
}
