/* stylelint-disable */

@mixin burger {
  --burger-width: 40px;
  --burger-height: 40px;
  --burger-line-height: 2px;

  position: relative;
  display: block;
  border-radius: 100px;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--light-color);
  background-color: var(--havelock-blue-color);
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    border-radius: 100px;
    width: 18px;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateX(-50%);
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    margin-top: -2px;
  }

  &::after {
    margin-top: 2px;
  }

  &--active {
    &::before {
      margin-top: 0;
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
      transition: transform 0.3s ease-in-out;
    }

    &::after {
      margin-top: 0;
      transform: translateX(-50%) translateY(-50%) rotate(-45deg);
      transition: transform 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}
