.service {
  padding: 0 10px 15px;
  &__list {
    gap: 20px;
  }

  &__item {
    border-radius: 20px;

    &-top {
      grid-column: 12 span;
      padding-top: 116px;
      padding-bottom: 132px;
      background: linear-gradient(221deg, #a8dec1 0%, #b7d7f0 100%),
        rgba(255, 255, 255, 0.4);

      @include high-tablet {
        padding-top: 62px;
        padding-bottom: 68px;
      }
    }

    &-bottom {
      position: relative;
      display: flex;
      grid-column: 6 span;
      padding-top: 51px;
      padding-left: 40px;
      min-height: 486px;

      @include high-desktop {
        padding-top: 30px;
        padding-left: 20px;
        min-height: 386px;
      }

      @include mobile {
        flex-direction: column;
        align-items: center;
        padding-left: 43px;
        padding-right: 43px;
      }

      &--left {
        @include image-set("../img/service-bt-img-1");
        background-position: center bottom 35px;
        background-size: 350px 300px;
        background-repeat: no-repeat;
        background-color: var(--ocean-green-color);

        @include high-desktop {
          background-size: 250px 200px;
        }

        @include mobile {
          background-position: center bottom 20px;
          background-size: 190px 159px;
        }
      }

      &--right {
        @include image-set("../img/service-bt-img-2");
        background-position: center bottom 50px;
        background-size: 300px 300px;
        background-repeat: no-repeat;
        background-color: var(--havelock-blue-color);

        @include high-desktop {
          background-size: 200px 200px;
        }

        @include mobile {
          background-position: center bottom 30px;
          background-size: 161px 161px;
        }
      }
    }

    @include high-tablet {
      grid-column: 8 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__image {
    display: block;
    margin: 0 auto;
    margin-bottom: 39px;

    @include high-tablet {
      margin-bottom: 28px;
      width: 157px;
      height: 60px;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 54px;
    line-height: 1.1;

    @include high-tablet {
      font-size: 30px;
      line-height: 1;
      letter-spacing: 0.02em;
    }

    @include mobile {
      font-size: 24px;
      text-align: center;
    }
  }

  &__tick {
    margin-right: 24px;
    width: 46px;
    height: 46px;
    fill: var(--light-color);

    @include high-desktop {
      width: 30px;
      height: 30px;
    }

    @include mobile {
      margin: 0;
      margin-bottom: 20px;
    }
  }

  &__text {
    max-width: 535px;
    font-weight: 500;
    font-size: 24px;
    color: var(--light-color);

    @include high-desktop {
      max-width: 400px;
      font-size: 20px;
    }

    @include mobile {
      font-size: 16px;
      text-align: center;
    }
  }
}
