.hero {
  margin: 10px;
  margin-bottom: 97px;
  border-radius: 20px;
  min-height: 753px;
  background: url("../img/dots-2.png") top/ cover no-repeat,
    url("../img/dots-1.png") bottom/ cover no-repeat,
    linear-gradient(
      314deg,
      #67b88d 0%,
      #33eb88 16.5%,
      #6df1aa 33.5%,
      #fff 53.5%,
      #5da0e2 100%
    ),
    rgba(255, 255, 255, 0.55);

  &__left {
    padding-top: 233px;
    grid-column: 7 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include high-tablet {
      grid-column: 8 span;
      padding-top: 145px;
      text-align: center;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__benefits {
    @include inline-flex-v-center;

    margin-bottom: 50px;
    padding: 26px 46px;
    border: 1px solid rgba(50, 51, 69, 0.31);
    border-radius: 90px;

    &-svg {
      margin-right: 8px;
      width: 18px;
      height: 20px;
      fill: var(--tuna-color);
    }

    &-desc {
      font-weight: 600;
      font-size: 14px;
      line-height: 1.2;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      color: var(--tuna-color);
    }

    @include high-tablet {
      display: none;
    }
  }

  &__title {
    margin-bottom: 50px;

    @include tablet {
      font-size: 44px;
      line-height: 1.1;
    }

    @include high-desktop {
      margin: 0 auto;
      margin-bottom: 34px;
      max-width: 400px;
      font-size: 40px;
    }

    @include mobile {
      font-size: 34px;
      line-height: 1.1;
      text-align: center;
    }
  }

  &__right {
    padding-top: 310px;
    grid-column: 5 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include high-tablet {
      grid-column: 8 span;
      padding-top: 53px;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  @include high-tablet {
    margin-bottom: 70px;
  }

  @include mobile {
    margin-left: 6px;
    margin-right: 6px;
    min-height: 700px;
  }
}
