.messagesSent {
  padding-top: 145px;
  padding-bottom: 170px;

  @include high-tablet {
    padding-bottom: 131px;
  }

  &__content {
    row-gap: 32px;

    @include high-tablet {
      row-gap: 16px;
    }
  }

  &__number {
    grid-column: 4 span;
    justify-self: end;
    font-weight: 700;
    font-size: 60px;
    line-height: 1;
    color: var(--havelock-blue-color);

    @include high-desktop {
      grid-column: 12 span;
      justify-self: center;
    }

    @include high-tablet {
      grid-column: 8 span;
      font-size: 50px;
    }

    @include mobile {
      grid-column: 6 span;
      font-size: 42px;
      letter-spacing: 0.02em;
    }
  }

  &__text {
    grid-column: 8 span;
    font-size: 60px;
    line-height: 1;

    @include high-desktop {
      grid-column: 12 span;
      justify-self: center;
    }

    @include high-tablet {
      grid-column: 8 span;
      font-size: 40px;
    }

    @include mobile {
      grid-column: 6 span;
      font-size: 28px;
      letter-spacing: 0.02em;
      text-align: center;
    }
  }

  &__desc {
    grid-column: 12 span;
    font-size: 20px;
    text-align: center;
    opacity: 0.7;

    @include high-tablet {
      grid-column: 8 span;
      font-size: 16px;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }
}
