.session {
  padding: 81px 0;
  margin-bottom: 81px;

  &__title {
    margin-bottom: 81px;
    font-size: 46px;
    text-align: center;

    @include high-tablet {
      margin-bottom: 36px;
      font-size: 40px;
    }

    @include mobile {
      font-size: 28px;
      line-height: 1;
      letter-spacing: 0.02em;
    }
  }

  &__list {
    position: relative;

    @include high-tablet {
      row-gap: 36px;
    }
  }

  &__item {
    grid-column: 4 span;

    &-line {
      position: absolute;
      top: 29%;
      left: 18%;
      z-index: -10;
      border: 1px dashed rgba(25, 27, 68, 0.23);
      width: 64%;
      height: 0px;

      @include high-tablet {
        display: none;
      }
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__img-block {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 27px;
    width: 120px;
    height: 120px;

    @include high-tablet {
      width: 100px;
      height: 100px;
    }

    @include mobile {
      margin-bottom: 20px;
      width: 70px;
      height: 70px;
    }
  }

  &__svg {
    @include high-tablet {
      width: 100px;
      height: 100px;
    }

    @include mobile {
      width: 70px;
      height: 70px;
    }
  }

  &__tick {
    position: absolute;
    right: 34px;
    bottom: 24px;
    width: 21px;
    height: 21px;
    fill: var(--havelock-blue-color);

    @include high-tablet {
      right: 49px;
      bottom: 40px;
    }

    @include mobile {
      right: 22px;
      bottom: 18px;
      width: 12px;
      height: 12px;
    }
  }

  &__desc {
    display: flex;
    flex-direction: column;
    font-size: 24px;
    text-align: center;

    @include tablet {
      font-size: 18px;
    }
  }

  @include mobile {
    padding: 0;
    padding-top: 60px;
  }
}
