.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;

  &__container {
    padding-top: 19px;
    padding-bottom: 10px;

    @include mobile {
      padding-top: 14px;
      padding-left: 12px;
      padding-right: 12px;
    }
  }

  &__wrap {
    @include flex-all-sb;

    @include high-tablet {
      position: relative;
      z-index: 1000;
      padding: 10px 8px;
      border-radius: 16px;
      background-color: rgba(255, 255, 255, 0.55);
    }
  }
}

.logo__image-tablet {
  @include high-tablet {
    width: 100px;
    height: 32px;
  }
}

.burger {
  display: none;

  @include high-tablet {
    @include burger;
  }
}
