.firstSession {
  margin: 0 10px;
  margin-bottom: 90px;
  padding: 129px 0;
  border-radius: 20px;
  background-color: var(--surf-crest-color);

  @include high-tablet {
    margin-bottom: 35px;
    padding: 0;
    padding-top: 69px;
    min-height: 905px;
  }

  @include mobile {
    margin-left: 6px;
    margin-right: 6px;
  }

  &__container {
    row-gap: 50px;
  }

  &__left {
    grid-column: 7 span;

    @include high-tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      grid-column: 8 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__duration {
    display: inline-block;
    margin-bottom: 39px;
    padding: 16px 20px;
    border: 1px solid rgba(30, 30, 30, 0.59);
    border-radius: 90px;

    &-desc {
      font-weight: 600;
      font-size: 12px;
      line-height: 1.2;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: var(--text-color);
    }

    @include high-tablet {
      margin-bottom: 36px;
    }

    @include mobile {
      padding: 16px;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin-bottom: 58px;
    font-size: 46px;
    line-height: 1.1;

    @include tablet {
      font-size: 38px;
      line-height: 1;
      letter-spacing: 0.02em;
    }

    @include high-tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 28px;
      text-align: center;
    }
  }

  &__notice {
    display: flex;
    gap: 20px;
    margin-bottom: 50px;

    &-percentage {
      margin-top: -9px;
      font-weight: 500;
      font-size: 100px;
      color: var(--havelock-blue-color);

      @include high-tablet {
        font-size: 60px;
      }
    }

    &-desc {
      display: flex;
      flex-direction: column;
      font-size: 24px;

      @include high-tablet {
        font-size: 16px;
      }
    }

    @include high-tablet {
      margin-bottom: 44px;
    }
  }

  &__right {
    position: relative;
    grid-column: 5 span;

    &-image {
      position: absolute;
      top: -47px;
      left: 0;

      @include tablet {
        top: 25%;
      }

      @include high-tablet {
        top: 50%;
        left: 50%;
        width: 338px;
        height: 410px;
        transform: translateX(-50%);
      }

      @include mobile {
        left: 54%;
        width: 294px;
        height: 360px;
      }
    }

    @include high-tablet {
      grid-column: 8 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }
}
