.questions__accordion {
  border-radius: 20px;
  background-color: var(--light-color);
  transition: background-color var(--accordion-time) ease-out;
  cursor: pointer;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:focus {
    outline: 2px solid var(--havelock-blue-color);
    outline-offset: 2px;
  }

  .accordion__control {
    display: flex;
    align-items: center;
    column-gap: 27px;
    padding: 22px 26px;
    padding-right: 0;
    border-radius: 20px;
    background-color: transparent;

    @include mobile {
      column-gap: 15px;
      padding: 20px 26px;
    }
  }

  .accordion__title {
    font-size: 24px;
    transition: color var(--accordion-time) ease-out;

    @include high-tablet {
      max-width: 576px;
      text-align: left;
      font-size: 20px;
      line-height: 1.1;
    }

    @include small-tablet {
      max-width: 427px;
    }

    @include mobile {
      font-size: 18px;
      max-width: 210px;
    }
  }

  .accordion__content {
    &-wrap {
      padding-left: 88px;
      padding-right: 20px;
      padding-bottom: 39px;

      @include mobile {
        padding-left: 61px;
        padding-bottom: 20px;
      }
    }
  }

  .accordion__icon {
    @include image-set("../img/accordion-img");
    @include bg-cover;

    display: inline-block;
    width: 35px;
    height: 35px;
    border-radius: 100%;
    background-size: 17px 17px;
    background-color: var(--havelock-blue-color);
    transition: transform var(--accordion-time) ease-out,
      background-color var(--accordion-time) ease-out;

    @include mobile {
      width: 26px;
      height: 26px;
      background-size: 12px 12px;
    }
  }

  .accordion__text {
    max-width: 550px;
    color: rgba(255, 255, 255, 0.85);

    @include mobile {
      font-size: 14px;
    }
  }
}

.is-open {
  background-color: var(--east-bay-color);
}

.is-open .accordion__icon {
  background-color: var(--ocean-two-color);
  transform: rotate(45deg);
}

.is-open .accordion__title {
  color: var(--light-color);
}
