.tariffs {
  padding: 90px 0;

  @include high-tablet {
    padding-top: 140px;
    padding-bottom: 70px;
  }

  &__title {
    margin-bottom: 64px;
    font-size: 46px;
    text-align: center;

    @include high-tablet {
      margin-bottom: 36px;
      font-size: 36px;
      line-height: 1;
      letter-spacing: 0.02em;
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &__list {
    &-small {
      display: flex;
      flex-direction: column;
      row-gap: 30px;
      margin-bottom: 50px;
    }
  }

  &__item {
    grid-column: 4 span;
    padding: 49px 50px 46px;
    border-radius: 20px;
    background-color: var(--surf-crest-color);

    @include high-desktop {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 20px 30px;
    }

    @include tablet {
      grid-column: 6 span;
    }

    @include high-tablet {
      grid-column: 2/6 span;
    }

    @include mobile {
      grid-column: 6 span;
      padding: 20px;
    }

    &-bay {
      background-color: var(--east-bay-color);
    }

    &-small {
      display: flex;
      align-items: center;
      column-gap: 17px;

      @include high-desktop {
        column-gap: 10px;
      }
    }
  }

  &__wrap {
    margin-bottom: 20px;
  }

  &__price {
    display: inline-block;
    padding: 5px 10px;
    border-radius: 21px;
    font-weight: 600;
    font-size: 10px;
    line-height: 1.2;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--seashell-color);

    @include mr(10px);

    &-discount {
      color: var(--seashell-color);
      background-color: var(--havelock-blue-color);
    }
  }

  &__caption {
    margin-bottom: 36px;
    font-size: 30px;

    &-white {
      color: var(--light-color);
    }

    @include mobile {
      margin-bottom: 34px;
      font-size: 28px;
    }
  }

  &__dot {
    margin-bottom: 3px;
    border-radius: 100%;
    width: 9px;
    height: 9px;
    fill: var(--dodger-blu-color);

    @include high-desktop {
      margin-bottom: 0;
    }

    &-white {
      fill: var(--light-color);
    }
  }

  &__text {
    font-size: 18px;
    opacity: 0.8;

    @include high-desktop {
      font-size: 16px;
    }

    &-white {
      color: var(--light-color);
    }
  }

  &__link {
    margin-bottom: 14px;
  }

  &__desc {
    text-align: center;
    opacity: 0.7;

    &-white {
      color: var(--light-color);
    }

    @include mobile {
      font-size: 14px;
    }
  }
}
