.ourAssistance {
  padding: 90px 0;

  @include high-tablet {
    position: relative;
    padding-top: 35px;
    padding-bottom: 55px;
  }

  &__container {
    position: relative;
    margin-bottom: 78px;

    @include high-tablet {
      margin-bottom: 36px;
    }
  }

  &__title {
    margin-bottom: 28px;
    font-size: 46px;

    @include high-tablet {
      margin-bottom: 24px;
      text-align: center;
      font-size: 36px;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      font-size: 28px;
      line-height: 1;
      letter-spacing: 0.02em;
    }
  }

  &__desc {
    display: block;
    font-size: 24px;

    @include high-tablet {
      text-align: center;
      font-size: 20px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  &__btns {
    position: absolute;
    top: 0;
    right: 0;
    width: 112px;
    height: 80px;

    &-top {
      @include high-tablet {
        display: none;
      }
    }

    &-bottom {
      display: none;

      @include high-tablet {
        top: 93%;
        right: 50%;
        display: block;
        transform: translateX(50%);
      }
    }
  }

  &__slider-btn {
    @include flex-all-center;

    border-radius: 37px;
    border: 1px solid var(--east-bay-color);
    width: 40px;
    height: 40px;
    color: var(--seashell-color);
    background-color: var(--east-bay-color);

    &::after {
      font-size: 12px;
    }

    &--next::after {
      margin-left: 1px;
    }

    &--prev::after {
      margin-right: 2px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border: 1px solid var(--havelock-blue-color);
      background-color: var(--havelock-blue-color);
    }

    &:active {
      background-color: var(--east-bay-color);
    }

    &.focus-visible {
      outline: 1px solid var(--east-bay-color);
      outline-offset: 3px;
    }
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__sliders {
    padding-top: 30px;
    padding-left: 35px;
    padding-bottom: 90px;
  }

  &__card {
    position: relative;
    padding-top: 66px;
    padding-left: 58px;
    border-radius: 20px;
    max-width: 413px;
    height: 504px;

    &-surf {
      background-color: var(--surf-crest-color);
    }

    &-east {
      background-color: var(--east-bay-color);
    }

    &-havelock {
      background-color: var(--havelock-blue-color);
    }

    &-content {
      max-width: 313px;
    }

    &-title {
      margin-bottom: 31px;
      font-size: 24px;

      @include high-desktop {
        font-size: 20px;
      }

      @include mobile {
        margin-bottom: 20px;
        font-size: 16px;
      }

      &_white {
        color: var(--seashell-color);
      }
    }

    &-quotes {
      width: 29px;
      height: 25px;
      margin-bottom: 17px;

      @include mobile {
        width: 15px;
        height: 11px;
      }

      &_white {
        fill: var(--seashell-color);
      }
    }

    &-text {
      @include mobile {
        font-size: 14px;
      }

      &_white {
        color: var(--seashell-color);
      }
    }

    &-smiley {
      position: absolute;
      right: -90px;
      bottom: -90px;
      width: 400px;
      height: 400px;

      @include high-desktop {
        right: -50px;
        bottom: -50px;
        width: 300px;
        height: 300px;
      }

      @include mobile {
        right: -40px;
        bottom: -30px;
        width: 250px;
        height: 250px;
      }

      &_dual {
        right: -30px;

        @include high-desktop {
          right: -10px;
        }
      }

      &_high {
        bottom: -60px;

        @include high-desktop {
          bottom: -30px;
        }
      }

      &_white {
        fill: var(--seashell-color);
      }
    }

    @include high-desktop {
      padding-top: 30px;
      padding-left: 20px;
    }
  }
}
