.footer {
  margin: 10px;
  margin-bottom: 0;
  padding: 60px 0 70px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  backdrop-filter: blur(6px);
  background: linear-gradient(224deg, #a8dec1 0%, #8fc4f8 100%),
    rgba(255, 255, 255, 0.23);

  @include mobile {
    margin: 6px;
    padding: 43px 0;
  }

  &__container {
    @include flex-all-sb;

    @include high-tablet {
      flex-direction: column;
      justify-content: flex-start;
      row-gap: 46px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
    row-gap: 12px;

    @include high-tablet {
      align-items: center;
      row-gap: 26px;
    }
  }

  &__center {
    display: flex;
    column-gap: 86px;

    @include high-tablet {
      flex-direction: column;
      row-gap: 20px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 30px;

    @include high-tablet {
      row-gap: 20px;
      align-items: center;
    }
  }

  &__link {
    font-weight: 500;
    line-height: 0;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--havelock-blue-color);
    }

    &:active {
      color: var(--ocean-green-color);
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__right {
    &-list {
      display: flex;
      flex-direction: column;
      row-gap: 30px;

      @include high-tablet {
        align-items: center;
        row-gap: 20px;
      }
    }
  }

  &__copyright {
    font-size: 14px;
    line-height: 1.4;
    color: rgba(30, 30, 30, 0.7);

    @include mobile {
      font-size: 12px;
    }
  }
}

.logo {
  &-destop {
    @include high-tablet {
      display: none;
    }
  }

  &-mobile {
    display: none;
    width: 77px;
    height: 74px;

    @include high-tablet {
      display: block;
    }
  }
}
