.nav {
  &__list {
    @include flex-all-center;

    gap: 30px;
    border-radius: 900px;
    padding: 26px 55px;
    backdrop-filter: blur(8px);
    background-color: rgba(255, 255, 255, 0.4);

    @include high-tablet {
      flex-direction: column;
      border-radius: 14px;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.7);
    }
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--havelock-blue-color);
    }

    &:active {
      color: var(--ocean-green-color);
    }

    @include high-tablet {
      font-size: 22px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  @include high-tablet {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.nav__link.focus-visible {
  outline: 1px solid var(--ocean-green-color);
  outline-offset: 3px;
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
