.questions {
  &__container {
    padding-top: 90px;
    padding-bottom: 60px;

    @include high-tablet {
      padding-top: 70px;
      padding-bottom: 70px;
    }
  }

  &__title {
    margin-bottom: 28px;
    font-size: 46px;

    @include high-tablet {
      margin-bottom: 24px;
      font-size: 36px;
      line-height: 1;
      letter-spacing: 0.02em;
      text-align: center;
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &__subtitle {
    display: block;
    margin-bottom: 44px;
    font-size: 24px;

    @include high-tablet {
      font-size: 16px;
      text-align: center;
      margin-bottom: 36px;
    }
  }

  &__top {
    row-gap: 66px;
    margin-bottom: 60px;
  }

  &__left {
    @include image-set("../img/questions-bg-img");

    grid-column: 6 span;
    margin-bottom: 56px;
    padding: 82px 50px;
    border-radius: 20px;
    background-position: right;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: var(--surf-crest-color);

    @include high-tablet {
      grid-column: 2/6 span;
      margin-bottom: 0;
    }

    @include mobile {
      grid-column: 6 span;
      padding: 60px 20px;
      min-height: 320px;
      background-position: right bottom;
      background-size: 176px 190px;
    }

    &-caption {
      margin-bottom: 36px;
      font-size: 30px;

      @include high-tablet {
        margin-bottom: 24px;
        font-size: 24px;
      }
    }

    &-text {
      max-width: 313px;
      font-size: 18px;

      @include high-tablet {
        font-size: 14px;
      }

      @include mobile {
        max-width: 175px;
      }
    }
  }

  &__right {
    grid-column: 8/5 span;

    @include high-tablet {
      grid-column: 2/6 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__address {
    font-style: normal;

    &-list {
      @include high-tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 39px;
      }
    }

    &-item {
      @include mb(44px);

      display: flex;
      column-gap: 34px;

      @include high-tablet {
        @include mb(0);

        flex-direction: column;
        align-items: center;
        row-gap: 30px;
      }
    }

    &-svg {
      width: 52px;
      height: 52px;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      row-gap: 5px;

      @include high-tablet {
        align-items: center;
      }
    }

    &-desc {
      font-weight: 500;
      font-size: 18px;
      line-height: 1.4;
      opacity: 0.4;
    }

    &-mail {
      font-weight: 500;
      font-size: 24px;
      line-height: 1.4;
      color: var(--text-color);
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: var(--havelock-blue-color);
      }

      &:active {
        color: var(--ocean-green-color);
      }

      @include mobile {
        font-size: 20px;
      }
    }

    &-text {
      font-weight: 500;
      font-size: 24px;

      @include high-tablet {
        display: block;
        text-align: center;
      }

      @include mobile {
        font-size: 20px;
      }
    }
  }
}
